import {debounce as _debounce, throttle as _throttle} from 'lodash-es';
// https://github.com/floating-ui/floating-ui/tree/v2.x
import {createPopper} from "@popperjs/core"
import {ElMessage} from 'element-plus'
import delegate from 'delegate'
import {isFunction, isObject} from '@/utils/types';
import {strLength} from '@/utils/validate'
import {splitStr, getActualWidthOfChars} from '@/utils'

/**
 * @description 防抖、节流指令的修饰符
 * @param {Event} event
 * @param {Object} modifiers 修饰符对象
 * @author fengjin
 * @date 2021-07-21 17:48:19
 */
function modifiersHandle(event, modifiers) {
  if (modifiers.prevent) event.preventDefault(); // 阻止默认事件
  if (modifiers.stop) event.stopPropagation(); // 阻止冒泡
}

/**
 * @description 防抖
 * @author fengjin
 * @date 2021-07-21 16:40:35
 * @link https://www.lodashjs.com/docs/lodash.debounce
 * @example
 * <button v-debounce.prevent="submitHandle"></button>
 * <button v-debounce:change="{fn: handle, delay: 1000}"></button>
 * <button v-debounce:change="{fn: handle, delay: 1000, leading: true}"></button>
 */
const debounce = {
  mounted(el, binding) {
    const {
      event = binding.arg || 'click',
      fn = () => {
      },
      delay = 200,
      ...options
    } = isFunction(binding.value) ? {
      fn: binding.value,
    } : binding.value;

    el.addEventListener(event, _debounce(e => {
      modifiersHandle(e, binding.modifiers);
      fn();
    }, delay, options), false);
  },
};

/**
 * @description 节流
 * @author fengjin
 * @date 2021-07-21 16:45:56
 * @link https://www.lodashjs.com/docs/lodash.throttle
 * @example
 * <button v-throttle.prevent="submitHandle"></button>
 * <button v-throttle:change="{fn: handle, delay: 1000}"></button>
 * <button v-throttle:change="{fn: handle, delay: 1000, leading: true}"></button>
 */
const throttle = {
  mounted(el, binding) {
    const {
      event = binding.arg || 'click',
      fn = () => {
      },
      delay = 1000,
      ...options
    } = isFunction(binding.value) ? {
      fn: binding.value,
    } : binding.value;

    el.addEventListener(event, _throttle((e) => {
      modifiersHandle(e, binding.modifiers);
      fn();
    }, delay, options), false);
  },
};
// 单行 超出文字显示...并显示文字提示框 只需要设置元素宽即可

const showTips = {
  // el {element} 当前元素
  mounted(el, binding) {
    const {
      delay = 300,
      place = 'top',
      hide = true,
      isOverflow = true,
    } = isObject(binding.value) ? binding.value : {}
    // 鼠标移入
    let setId = null
    let tooltipId = null
    el.classList.add('n-only-tooltip')
    let fn = _throttle(function (e) {
      /* console.log({
         bool: !e.relatedTarget.matches('.n-only-tooltip') && !e.target.matches('.n-only-tooltip'),
         // bool: !e.path.some(item => /n-only-tooltip/.test(item.className)),
         target: e.target,
         relatedTarget: e.relatedTarget,
         path: e.path,
         className: e.path.map(item => item.className)
       })*/
      // mousemove 事件
      // if (e.path.some(item => /n-only-tooltip/.test(item.className))) {
      if (e.relatedTarget?.matches('.n-only-tooltip') && e.target.matches('.n-only-tooltip')) {
        clearTimeout(tooltipId)
      }
      tooltipId = setTimeout(() => {
        // mousemove 事件
        // if (!e.path.some(item => /n-only-tooltip/.test(item.className))) {
        if (!e.relatedTarget?.matches('.n-only-tooltip') && !e.target.matches('.n-only-tooltip')) {
          let tooltip = document.getElementById('tooltip')
          window.removeEventListener('mouseover', fn)
          if (tooltip) {
            tooltip.onmouseleave = null
            tooltip.remove()
          }
          clearTimeout(tooltipId)
          tooltipId = null
        }
      }, 300)
    }, 300)
    // 给当前元素设置超出隐藏
    el.style.overflow = 'hidden'
    el.style.textOverflow = 'ellipsis'
    el.style.whiteSpace = 'nowrap'
    // 鼠标移入
    el.onmouseenter = function (e) {
      // scrollWidth获取总宽度 包含超出  显示提示框
      let textWidth = getActualWidthOfChars(el.innerText)
      // if (el.scrollWidth > el.offsetWidth) {
      if (el.innerText && (textWidth > el.offsetWidth || el.scrollWidth > el.offsetWidth)) {
        el.style.cursor = 'pointer'
        clearTimeout(setId)
        setId = setTimeout(() => {
          let tooltip = document.getElementById('tooltip')
          tooltip && tooltip.remove()
          // 创建浮层元素并设置样式
          const div = document.createElement('div')
          // const i = document.createElement('i')
          div.classList.add('tooltip', 'n-only-tooltip')
          // i.classList.add('tooltip-arrow', 'n-only-tooltip')
          // 样式在src/styles/scss/index.scss
          // 设置id方便寻找
          div.setAttribute('id', 'tooltip')
          // 浮层中的文字
          div.innerHTML = `<div class="${isOverflow ? 'tooltip-overflow ' : ''}n-only-tooltip">${el.innerText}</div>`
          // div.appendChild(i)
          div.onmouseleave = function () {
            clearTimeout(tooltipId)
            tooltipId = null
            this.onmouseleave = null
            this.remove()
          }
          // 将浮层插入到body中
          document.body.appendChild(div)
          // 让提示元素的位置始终保持在正确位置
          createPopper(this, div, {
            strategy: 'fixed',
            placement: place,
            modifiers: [
              {
                name: 'offset',
                options: {
                  offset: [0, 8],
                },
              },
              /*{
                name: 'arrow',
                options: {
                  element: i,
                },
              },*/
            ],
          });
          clearTimeout(setId)
          setId = null
        }, delay)
      }
    }
    // 鼠标移出
    el.onmouseleave = function () {
      el.style.cursor = 'default'
      if (hide) {
        let id = setTimeout(() => {
          // 找到浮层元素并移除
          const tooltip = document.getElementById('tooltip')
          tooltip && document.body.removeChild(tooltip)
          clearTimeout(id)
          id = null
        }, 300)
        return
      }
      window.addEventListener('mouseover', fn)
    }
  },
  // 指令与元素解绑时
  beforeUnmount(el) {
    el.onmouseenter = null
    el.onmouseleave = null
    // 找到浮层元素并移除
    let tooltip = document.getElementById('tooltip')
    tooltip && tooltip.remove()
  }
}
/*
*  v-auto-tooltip 存在省略号的时候自动出现弹出窗
*  tooltip-target-class="text_content" 需要出现提示框的目标元素类名（可不指定，不指定默认为指令所在的元素）
*  tooltip-custom-class 弹出窗自定义类名（可不指定）
* */
const autoTooltip = {
  mounted(el) {
    const div = document.createElement('div')
    const i = document.createElement('i')
    div.classList.add('tooltip')
    div.classList.add('multi-ellipsis--l2')
    const customClass = el.getAttribute('tooltip-custom-class')
    if (customClass) {
      div.classList.add(customClass)
    }
    i.classList.add('tooltip-arrow')
    let popper = null
    const tooltipTarget = el.getAttribute('tooltip-target-class')
    /*if (tooltipTarget) {
      el = el.getElementsByClassName(tooltipTarget)[0]
      if (!el) {
        console.error('需要绑定的目标不存在')
      }
    }*/
    // 鼠标移入
    el.addEventListener('mouseenter', () => {
      let els = el
      if (tooltipTarget) {
        els = el.getElementsByClassName(tooltipTarget)[0]
        if (!els) {
          return false
        }
      }
      if (els.scrollHeight > els.offsetHeight || els.scrollWidth > els.offsetWidth) {
        // 浮层中的文字
        // div.innerText = els.innerText
        div.innerHTML = `<div class="tooltip-overflow">${els.innerText}</div>`
        if (popper) {
          div.style.display = 'block'
        } else {
          div.appendChild(i)
          document.body.appendChild(div)
          popper = createPopper(els, div, {
            placement: 'top',
            strategy: 'fixed',
            removeOnDestroy: true,
            modifiers: [
              {
                name: 'offset',
                options: {
                  offset: [0, 10],
                },
              },
              {
                name: 'arrow',
                options: {
                  element: i,
                },
              },
            ],
          });
          el.popper = popper
        }
      }
    })
    // 鼠标移出
    el.addEventListener('mouseleave', () => {
      // popper.destroy()
      if (popper) div.style.display = 'none'
    })
  },
  beforeUnmount(el) {
    if (el.popper) el.popper.destroy()
  }
}

let delegation = null
/**
 * @description 事件委托 精准委托
 * @author 王应龙
 * @date 2021-08-23 14:38:10
 * @link https://github.com/zenorocha/delegate
 * @returns 返回目标元素
 * @example
 * <button v-eventDelegation="{fn: handler, target: '触发目标类名'}"></button>
 * <button v-eventDelegation:change="{fn: handler, target: '触发目标类名'}"></button>
 */
const eventDelegation = {
  // el {element} 当前元素
  mounted(el, binding) {
    let event = binding.arg || 'click'
    const {
      target = false,
      fn = (e) => {
        console.log(e, '触发元素')
      },
    } = binding.value
    if (target) {
      delegation = delegate(el, target, event, function (e) {
        fn(e.delegateTarget)
      }, false);
    }
  },
  // 指令与元素解绑时
  beforeUnmount() {
    // 找到浮层元素并移除
    delegation.destroy();
  }
}

